import React from 'react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, Legend } from 'recharts';

const van_data_chart = [
  { year: 1867, event: "Sinh ra ti Ba Lan", achievement: 0, perseverance: 10, obstacle: "Sng di s chim ng ca Nga" },
  { year: 1891, event: "Chuyn n Paris hc ti Sorbonne", achievement: 20, perseverance: 30, obstacle: "Kh khn ti chnh khi hc tp  nc ngoi" },
  { year: 1898, event: "Pht hin ra polonium v radium", achievement: 50, perseverance: 60, obstacle: "Phi i mt vi s nghi ng t cng ng khoa hc" },
  { year: 1903, event: "Nhn gii Nobel Vt l", achievement: 70, perseverance: 80, obstacle: "B k th gii tnh trong lnh vc khoa hc" },
  { year: 1911, event: "Nhn gii Nobel Ha hc", achievement: 90, perseverance: 95, obstacle: "Khng hong c nhn v s nghi ng t cng chng" },
  { year: 1934, event: "Qua i v phi nhim phng x", achievement: 100, perseverance: 100, obstacle: "Tc ng lu di ca nghin cu phng x n sc khe" }
];

const TimelineChart = () => {
  return (
    <ResponsiveContainer width="100%" height={400}>
      <LineChart
        data={van_data_chart}
        margin={{
          top: 10, right: 30, left: 20, bottom: 5,
        }}
      >
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis dataKey="year" />
        <YAxis />
        <Tooltip formatter={(value, name, props) => {
          const { payload } = props;
          return [
            `Thnh tu: ${payload.achievement}% | Kin tr: ${payload.perseverance}%`,
            `S kin: ${payload.event}, Tr ngi: ${payload.obstacle}`
          ];
        }} />
        <Legend />
        {/* ng biu din achievement (Thnh tu) */}
        <Line type="monotone" dataKey="achievement" stroke="#8884d8" name="Thnh tu" activeDot={{ r: 8 }} />
        {/* ng biu din perseverance (Kin tr vt qua kh khn) */}
        <Line type="monotone" dataKey="perseverance" stroke="#82ca9d" name="Kin tr" activeDot={{ r: 8 }} />
      </LineChart>
    </ResponsiveContainer>
  );
}

export default TimelineChart;
